.blockMessenger{
    width: 60px;
    height: 60px;
    border: 1px solid #423;
    border-radius: 50%;
    background-color: #333;
}
.blockMessenger a {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    margin: 0;
}
.imgMessenger {
    background-position: center;
    background-size: 30px;
    background-repeat: no-repeat;
}